//  将2017-08-01转为8月1日
function getLocalDate(str) {
    var arr = str.split('-');
    return parseInt(arr[1]) + '月' + parseInt(arr[2]) + '日';
}

// 将11:35:00 转为11:35
function getLocalHour(str) {
    var arr = str.split(':');
    var hour = parseInt(arr[0]) + 8;
    var minute = parseInt(arr[1]) === 0 ? arr[1] : parseInt(arr[1]);
    hour = hour < 24 ? hour : hour - 24;
    return hour + ':' + minute;
}

// 格式化htmlString
function getHTMLText(str) {
    if (!str) {
        return '';
    }
    return str.replace(/<\/?[^>]*>/g, '') // 取出tags
    .replace(/[ | ]*\n/g, '\n') // 去除行尾空白
    .replace(/&nbsp;/ig, '') // 去掉&nbsp;
    .replace(/\s/g, ''); // 将空格去掉
}

